<template>
  <div class="register">
    <header>
      <i @click="back"></i>
    </header>
    <div class="registerbox">
      <div class="title">注册</div>
      <div class="form-box">
        <div class="input phoneinp">
          <input class="inptext" type="text" placeholder="请输入手机号码" v-model="user.phone" />
        </div>
        <div class="input uinp">
          <input class="inptext" type="text" placeholder="请输入昵称(不宜过长)" v-model="user.nickname" />
        </div>
        <div class="input uinp">
          <input class="inptext" type="password" placeholder="密码长度6-20位" v-model="user.pwd" />
        </div>
        <div class="codeinp clearfix">
          <div class="fl input code-input">
            <input class="inptext" type="text" placeholder="验证码" v-model="user.code" />
          </div>
          <div class="fl code-btn" @click="getCode">{{codeContent.txt}}</div>
        </div>

        <div class="register-btn" @click="register">注册</div>
      </div>
    </div>  
    <div class="protocol">注册即代表您同意我们的 <a href="">《注册协议》</a></div>
  </div>
</template>

<script>
  import{valid} from '../assets/js/valid'  // 导入valid.js

  export default {
    name:'register',

    data(){
      return{
        // 确保this.axios({})关联成功，定义对象 —— 绑定在html上 _ ajax中传入params
        user:{
          phone:'', //手机号19849042805
          code: '',
          pwd:'',
          nickname:''
        },

        codeContent: {
          txt: '获取验证码',
          time: 5
        }
      }
    },

    methods:{
      // 获取验证码
      getCode(){
        // 手机成功收到短信关闭发短信功能 —— 导入 证验手机号 (创建assets文件夹并在js文件夹下创建valid.js)
        //验证手机号
        if (!valid.isPhone(this.user.phone)) {
          this.$notify({
            type: 'warning',
            message: '手机号码格式不正确'
          });
          return;
        }
        this.codeContent.txt = this.codeContent.time + 's后重新获取';

        let timer = setInterval(() => {

          this.codeContent.time--;
          if (this.codeContent.time == -1) {
            clearInterval(timer);
            timer = null;
            this.codeContent.txt = '获取验证码';
            this.codeContent.time = 5;
          }else{
            this.codeContent.txt = this.codeContent.time + 's后重新获取';
          }
        },1000) //定时每个一秒变化一次 —— 后台创建用户表user

        // console.log('this.user.phone=>',this.user.phone);
        this.axios({
          method:'GET',
          // url:'http://127.0.0.1:10008/getMessageCode', //拼接后台路由控制路径
          url: '/getMessageCode', //路径在main.js中
          params:{
            phone: this.user.phone
          }
        }).then(result =>{
          // console.log('getCode result=>',result);

          // 判读存贮cookie
          if (result.data.status == 20000) {
              // console.log('coming');
              //存储cookie   / this.$cookies.set(键名, 值, 过期时间)
              this.$cookies.set(result.data.__ctk.key, result.data.__ctk.ctk, result.data.time);

          }
        }).catch(err =>{
          // console.log('err出错！');
        })
      },

      //注册
      register(){
        this.$toast({
          duration: 0,
          message: '注册中...'
        });

        this.axios({
          method: 'POST',
          url: '/appRegister', //路径设置放在main.js中
          
          //post请求参数
          data: {
            phone: this.user.phone,
            key: 'code',
            code: this.user.code,
            pwd:this.user.pwd,
            nickname:this.user.nickname
          }
        }).then(result =>{
          // console.log('register result =>',result);    
          
          //保存登录验证token
          if (result.data.status == 1000) {
            this.$cookies.set(result.data.__utk.key, result.data.__utk.utk, result.data.time * 24 * 60 * 60);
          }
          
          this.$toast.clear(); //清除加载提示

          //注册成功提示
          this.$notify({
            type: result.data.status == 1000 ? 'success' : 'warning',
            message: result.data.msg,

            //当前关闭提示后跳转
            onClose: () => {
              if (result.data.status == 1000) {
                this.$router.push({name: 'pwdlogin'});
              }
            }
          });

        }).catch( err=>{
          // console.log('注册出错');
          this.$toast.clear();
        })

      },

      // 返回
      back(){
        this.$router.push({name: 'login'});
      }
    }
  }
</script>

