class Valid {

  // 构造
  constructor() {
    this.phoneReg = /^1[3456789]\d{9}$/;

    this.pwdReg =/^(?=.*[a-z])\w{6,16}$/; 
  }

  //验证手机号
  isPhone(value) {
    return this.phoneReg.test(value);
  }

  // 验证密码
  isPwd(v){
    return this.pwdReg.test(v);
  }

}

export const valid = new Valid();